import { Injectable } from '@angular/core';
import { ngxCsv } from 'ngx-csv/ngx-csv';
@Injectable({
  providedIn: 'root'
})
export class CSVServiceService {

  constructor(private csvservice:CSVServiceService) { }


 Export(data:any, _header: any[],_title:string){
  var options = { 
    fieldSeparator: ',',
    quoteStrings: '"',
    decimalseparator: '.',
    showLabels: false, 
    showTitle: true,
    title: _title,
    useBom: false,
    noDownload: false,
    headers: _header
  };
 new ngxCsv(data, 'My Report', options);

 }


  }
  

