import { Injectable, Inject } from '@angular/core';
import {HttpClient, HttpResponse, HttpErrorResponse} from '@angular/common/http'
import { RepairSearch } from 'app/Models/repair-search.model';
import { Repair } from 'app/Models/repair.model';
import { FileModel } from 'app/Models/file-model.model';
import { RepairDateModel } from 'app/Models/repair-date-model.model';

@Injectable({
  providedIn: 'root'
})
export class RepairService {

  public HostUrl: string;
  public SelectedSearchModel: RepairSearch = { } as any;
  public SelectedRepairModel: Repair = { } as any;
  

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string) {
    this.HostUrl=baseUrl;

   }


   async GetRepairSearchList() {
    let list: RepairSearch[] ;
    await this.http.get(  this.HostUrl + 'api/Repair/GetRepairSearchList')   .toPromise()
    .then(res => list = res as RepairSearch[])
    return list;
  }


  async GetRepairlist(vehicleID: number) {
    let repairList: Repair[] ;
    await this.http.get(  this.HostUrl + 'api/repair/GetRepairList/' + vehicleID)   .toPromise()
    .then(res => repairList = res as Repair[])
    return repairList;
  }

   async GetRepairlistByDate(Model: RepairDateModel) {
   
    return await  this.http.post(  this.HostUrl + 'api/repair/GetRepairListByDate' , Model);
  }

  async GetImages(id:number) {
   let FileList : FileModel[];
    await this.http.get(  this.HostUrl + 'api/repair/GetImage/' + id)   .toPromise()
    .then(res => FileList = res as FileModel[])
    return FileList;
  }

  AddUpdateRepair(rModel: FormData) {

   
    let date= this.AdjustDate( rModel.get('Date'));
   rModel.set('Date', date.toDateString());
  

   if(rModel.get('NextAppointment') !== null )
   {
    let NextAppointment = this.AdjustDate(rModel.get('NextAppointment'));
    rModel.set('NextAppointment',NextAppointment.toDateString());
   }
   else
   {
    rModel.set('NextAppointment', null)
   }
 


   return this.http.post(this.HostUrl + 'api/repair/AddUpdateRepair', rModel);
  }

  UploadFile(FileList: FormData) {
    return this.http.post(this.HostUrl + 'api/repair/UploadFile', FileList);
  }

  AdjustDate(dateToAdjust):Date {
  let newDate= new Date(dateToAdjust);
//newDate.setMinutes(newDate.getMinutes() + (2*newDate.getTimezoneOffset()));
return newDate;
    }





    DeleteImage(id:number) {
     
     return this.http.get(this.HostUrl + 'api/repair/DeleteImage/'+id);
    }

    DeleteVisit(id:number) {
     
      return this.http.get(this.HostUrl + 'api/repair/DeleteVisit/'+id);
     }
}
