import { Component, OnInit, ViewChild } from '@angular/core';
import { CustomerService } from 'app/Services/customer.service';
import { Customer } from 'app/Models/customer.model';
import { NotificationService } from 'app/Services/notification.service';
import { HttpErrorResponse } from '@angular/common/http';
import { Vehicle } from 'app/Models/vehicle.model';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { NgForm, FormControl } from '@angular/forms';
import { CSVServiceService } from 'app/Services/csvservice.service';
import { map, startWith } from 'rxjs/operators'
import { Observable } from 'rxjs';
import { filter } from 'rxjs-compat/operator/filter';
import { SettingServiceService } from 'app/Services/setting-service.service';
import Swal from 'sweetalert2';
@Component({
  selector: 'app-customer-setting',
  templateUrl: './customer-setting.component.html',
  styles: []
})
export class CustomerSettingComponent implements OnInit {

  @ViewChild(MatPaginator, { static: false }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: false }) sort: MatSort;
  dataSource: any;
  displayedColumns: string[];
  public VehicleList: any;


  public myControl = new FormControl();
  public myControlModel = new FormControl();
  filteredOptions: Observable<string[]>;
  filteredOptionsModel: Observable<string[]>;
  public aCustomer: Customer = {} as any;
  public aVehicle: Vehicle = {} as any;
    // tslint:disable-next-line: max-line-length
  public Makes: string[] = {} as any; // =['Audi','Toyota', 'BMW','Audi','Toyota', 'BMW','Audi','Toyota', 'BMW','Audi','Toyota', 'BMW','Audi','Toyota', 'BMW','Audi','Toyota', 'BMW'];
  public Models: string[] = {} as any;
  // tslint:disable-next-line: max-line-length
  constructor(private customerService: CustomerService, private echo: NotificationService, private csvService: CSVServiceService, private settingService: SettingServiceService) { }

  async ngOnInit() {
  //  this.aCustomer.ID = -1;
    this.Makes = await this.settingService.GetActiveMakeList();
    this.aCustomer = this.customerService.SelectctedCustomer;
   // console.log(this.aCustomer.ID);
    if (this.aCustomer.ID !== -1 && this.aCustomer.ID!==undefined ) {
      this.VehicleList = await this.customerService.GetVehicleList(this.aCustomer.ID);
      this.dataSource = new MatTableDataSource(this.VehicleList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.displayedColumns = ['Year', 'Make', 'Model',   'EngineSize', 'VIN', 'Description', 'Actions'];
    }

    this.filteredOptions = this.myControl.valueChanges.pipe(startWith(''),
      map(value => this._filter(value))

    );


  }

  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.Makes.filter(option => option.toLowerCase().includes(filterValue));
  }

  private _filterModel(value: string): string[] {
    const filterValue = value.toLowerCase();
    return this.Models.filter(option => option.toLowerCase().includes(filterValue));
  }


  AddUpdateCustomer() {
    this.customerService.AddUpdateCustomer(this.aCustomer).subscribe(
      res => {
        let response = res as any;
        this.aCustomer.ID = response.CustomerID;
        this.echo.Success(response.Message);

      },
      err => {
        // console.log(err);
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },
    );
  }



  async AddUpdateVehicle(form: NgForm) {
    this.aVehicle.CustomerID = this.aCustomer.ID;
    this.customerService.AddUpdateVehicle(this.aVehicle).subscribe(
      async  res => {
        this.dataSource = new MatTableDataSource(await this.customerService.GetVehicleList(this.aCustomer.ID));
        this.dataSource.paginator = this.paginator;
        this.dataSource.sort = this.sort;
        this.displayedColumns = ['Year', 'Make', 'Model',   'EngineSize', 'VIN', 'Description', 'Actions'];
        this.echo.Success(res);
        this.Reset(form);


      },
      err => {
         console.log(err);
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },


    );
  }

  Reset(form: NgForm){

    this.aVehicle.ID=-1;
    this.aVehicle.Make='';
    this.aVehicle.Model='';
    this.aVehicle.Description='';
    this.aVehicle.EngineSize='';
    this.aVehicle.VIN='';
    this.aVehicle.Year='';
    form.resetForm();
  }


  Edit(aVehicle: Vehicle) {
    this.aVehicle = aVehicle;
  }

  
  async DeleteVehicle(VehicleID: number)
  {

     Swal.fire({
        title: 'Are you sure?',
        text: "You won't be able to revert this!",
        icon: 'warning',
        showCancelButton: true,
        confirmButtonColor: '#00bcd4',
        cancelButtonColor: '#d33',
        focusConfirm: false,
        confirmButtonText: 'Yes, delete it!'
      }).then((result) => {
        if (result.value) {

          this.DeleteVehicleInner(VehicleID);

        }

      });

  }

 async DeleteVehicleInner(VehicleID: number)
  {
  
    this.customerService.DeleteVehicle(VehicleID).subscribe(
      async res => {

       this.echo.Success(res);
       this.VehicleList = await this.customerService.GetVehicleList(this.aCustomer.ID);
       this.dataSource = new MatTableDataSource(this.VehicleList);
       this.dataSource.paginator = this.paginator;
       this.dataSource.sort = this.sort;
       this.displayedColumns = ['Year', 'Make', 'Model',   'EngineSize', 'VIN', 'Description', 'Actions'];

       },
       err => {
          console.log(err);
         let e = err as HttpErrorResponse;
         this.echo.Error(e.error.Value);
       },
     );
  }

  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }

  IsCustomerExist() {
    if (this.customerService.SelectctedCustomer.ID === -1 || this.aCustomer.ID === undefined) {
      return false;
    }
    return true;
  }



  ExportCSV() {
    var date = new Date();
    let reportHeader = ['VehicleID', 'CustomerID', 'Make', 'Model', 'Year', 'Note', 'Engine Size', 'VIN'];
    this.csvService.Export(this.VehicleList, reportHeader, 'Vehicle List of' + this.aCustomer.FirstName + ' ' + this.aCustomer.MiddleName + ' ' + this.aCustomer.LastName + '  | ' + date.toString());
  }


 async OnFocusModel() {

if(this.aVehicle.Make!==''){
    this.Models = await this.settingService.GetModelList(this.aVehicle.Make);

    this.filteredOptionsModel = this.myControlModel.valueChanges.pipe(startWith(''),
    map(value => this._filterModel(value))

  );
}
  }


}
