import { Component, OnInit, ViewChild } from '@angular/core';
import { RepairService } from 'app/Services/repair.service';
import { NotificationService } from 'app/Services/notification.service';
import { MatPaginator, MatSort, MatTableDataSource } from '@angular/material';
import { RepairSearch } from 'app/Models/repair-search.model';
import { Router } from '@angular/router';
import { CSVServiceService } from 'app/Services/csvservice.service';

@Component({
  selector: 'app-vehicle-list',
  templateUrl: './vehicle-list.component.html',
  styles: []
})
export class VehicleListComponent implements OnInit {

  @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;
  @ViewChild(MatSort, {static: false}) sort: MatSort;
    dataSource: any;
    displayedColumns: string[];
public RepairSearchList: any;

    constructor(private service: RepairService, private _router:Router, private csvService:CSVServiceService) {   }

    async ngOnInit() {
   
  this.RepairSearchList=await this.service.GetRepairSearchList();
      this.dataSource = new MatTableDataSource(this.RepairSearchList);
      this.dataSource.paginator = this.paginator;
      this.dataSource.sort = this.sort;
      this.displayedColumns = ['CustomerFullName','Phone','Year','Make', 'Model',  'EngineSize', 'Description','Actions'];

     }
   
     applyFilter(filterValue: string) {
       this.dataSource.filter = filterValue.trim().toLowerCase();
     }

     Edit(searchModel: RepairSearch)
     {
       this.service.SelectedSearchModel=searchModel;
       this._router.navigate(['repair/setting'])
     }


     ExportCSV(){
      var date = new Date();
     let reportHeader= ['VehicleID','CustomerID','Customer','Phone','Vehicle Make','Model','Year','Note','Engine Size','VIN'];
      this.csvService.Export(this.RepairSearchList,reportHeader,'Visiting list  | '+date.toString());
    }

}
