import { Component, OnInit, ViewChild } from '@angular/core';
import { MatPaginator, MatTableDataSource } from '@angular/material';
import { SettingServiceService } from 'app/Services/setting-service.service';
import { NotificationService } from 'app/Services/notification.service';
import { MakeModel } from 'app/Models/make-model.model';
import { HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-setting',
  templateUrl: './setting.component.html',
  styles: []
})
export class SettingComponent implements OnInit {
  @ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;

  dataSource: any;
  displayedColumns: string[];
  public VehicleList:  any;



  public MakeList:MakeModel[] = {} as any;
  public aMake: MakeModel = {} as any;

  public Makes:string[]=['Make','Model', 'Active'];

 
 
  constructor(private settingService: SettingServiceService, private echo:NotificationService) { }



 async ngOnInit() {

 this.MakeList=await this.settingService.GetAllMakeList();
   this.dataSource = new MatTableDataSource(this.MakeList);
   this.dataSource.paginator = this.paginator;
   this.displayedColumns = ['Make', 'Model', 'Active', 'Actions'];
 
 
  }

  Edit(m){
    this.aMake=m;
  }

  async AddUpdateVehicle(form){

   
      this.settingService.AddUpdateMake(this.aMake).subscribe(
       async res => {
       

          this.echo.Success(res);
          this.MakeList=await this.settingService.GetAllMakeList();
   this.dataSource = new MatTableDataSource(this.MakeList);
   this.dataSource.paginator = this.paginator;
   this.displayedColumns = ['Make', 'Model', 'Active', 'Actions'];
  
  
          this.Clear(form);
        },
        err => {
           console.log(err);
          let e = err as HttpErrorResponse;
          this.echo.Error(e.error.Value);
        },
      );

  }

  Clear(form:NgForm){
    form.resetForm();
    this.aMake.Active=false;
    this.aMake.Make='';
    this.aMake.Model='';
    this.aMake.ID=-1;
    
    }

}
