import { Component, OnInit, ViewChild } from '@angular/core';
import { CustomerService } from 'app/Services/customer.service';
import { MatTableDataSource, MatSort, MatPaginator } from '@angular/material';
import { NotificationService } from 'app/Services/notification.service';
import { async } from 'q';
import { Customer } from 'app/Models/customer.model';
import { Router } from '@angular/router';
import { ThrowStmt } from '@angular/compiler';
import { CSVServiceService } from 'app/Services/csvservice.service';
import Swal from 'sweetalert2';
import { HttpErrorResponse } from '@angular/common/http';

@Component({
  selector: 'app-customer-list',
  templateUrl: './customer-list.component.html',
  styles: []
})
export class CustomerListComponent implements OnInit {

@ViewChild(MatPaginator, {static: false}) paginator: MatPaginator;
@ViewChild(MatSort, {static: false}) sort: MatSort;
  public dataSource: any;
  public CustomerList: any;
  displayedColumns: string[];

  constructor(private service: CustomerService, private _router: Router, private csvService:CSVServiceService,private echo: NotificationService) { }

 async ngOnInit() {

this.ResetCustomer();
this.CustomerList= await this.service.GetAllCustomers();

   this.dataSource=new MatTableDataSource(this.CustomerList);
    this.dataSource.paginator = this.paginator;
    this.dataSource.sort = this.sort;
    this.displayedColumns = ['FirstName', 'MiddleName', 'LastName', 'Phone', 'Street', 'City', 'State','Actions'];


  }

  
  applyFilter(filterValue: string) {
    this.dataSource.filter = filterValue.trim().toLowerCase();
  }



 Edit(aCustomer: Customer){


  this.service.SelectctedCustomer = aCustomer;
  this._router.navigate(['customer/setting'])

}

ExportCSV(){
  var date = new Date();
 let reportHeader= ['ID','First Name','Middle Name','Last Name','Phone','Street','City','State'];
  this.csvService.Export(this.CustomerList,reportHeader,'Customer List | '+date.toString());
}

ResetCustomer(){
  this.service.SelectctedCustomer.ID=-1;
  this.service.SelectctedCustomer.FirstName='';
  this.service.SelectctedCustomer.MiddleName='';
  this.service.SelectctedCustomer.LastName='';
  this.service.SelectctedCustomer.Email='';
  this.service.SelectctedCustomer.Phone='';
  this.service.SelectctedCustomer.City='';
  this.service.SelectctedCustomer.Street='';
  this.service.SelectctedCustomer.State='';
}



async DeleteCustomer(CustomerID: number)
{

   Swal.fire({
      title: 'Are you sure?',
      text: "You won't be able to revert this!",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#00bcd4',
      cancelButtonColor: '#d33',
      focusConfirm: false,
      confirmButtonText: 'Yes, delete it!'
    }).then((result) => {
      if (result.value) {

        this.DeleteCustomerInner(CustomerID);

      }

    });

}

async DeleteCustomerInner(CustomerID: number)
{

  this.service.DeleteCustomer(CustomerID).subscribe(
    async res => {

     this.echo.Success(res);
     this.CustomerList= await this.service.GetAllCustomers();
     this.dataSource=new MatTableDataSource(this.CustomerList);
     this.dataSource.paginator = this.paginator;
     this.dataSource.sort = this.sort;
     this.displayedColumns = ['FirstName', 'MiddleName', 'LastName', 'Phone', 'Street', 'City', 'State','Actions'];

     },
     err => {
        console.log(err);
       let e = err as HttpErrorResponse;
       this.echo.Error(e.error.Value);
     },
   );
}

}
