import { Component, OnInit, Console } from '@angular/core';
import { UserModel } from 'app/Models/user-model.model';
import { UserService } from 'app/Services/user.service';
import { NotificationService } from 'app/Services/notification.service';

import { Router } from '@angular/router';
import { HttpErrorResponse } from '@angular/common/http';
import { NgForm } from '@angular/forms';

@Component({
  selector: 'app-user',
  templateUrl: './user.component.html',
  styles: []
})
export class UserComponent implements OnInit {


  NewUserModel: UserModel;
  UpdateUserModel: UserModel;
  ConfirmPassword: string;
  UpdateUserNameHintText:string;
  NewUserNameHintText:string;

  constructor(private service: UserService, private echo: NotificationService, private _route: Router, private _userModel: UserModel) {
    this.NewUserModel = _userModel;
    this.UpdateUserModel = _userModel;
    this.UpdateUserNameHintText="";
  }

  ngOnInit() {
    let userName=sessionStorage.getItem('UserName');
    this.service.GetUserByName(userName).toPromise().then(
      res => this.UpdateUserModel = res as UserModel
    );

    
  }




  AddNewUser(form: NgForm) {

    this.service.AddUpdateUser(this.NewUserModel).subscribe(
      res => {

        this.echo.Success(res);
        this.Clear(form);
      },
      err => {
        //console.log(err);
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },


    );
  }

  UpdateUser() {

    this.service.AddUpdateUser(this.UpdateUserModel).subscribe(
      res => {

        this.echo.Success(res);


      },
      err => {
        // console.log(err);
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },


    );
  }

  IsMatched() {
    if (this.NewUserModel.Password !== this.ConfirmPassword)
    {
      return false;
    }
    return true;
  }


Clear(form:NgForm){
form.resetForm();
  this.NewUserModel.Password = '';
  this.NewUserModel.Email = '';
  this.NewUserModel.FirstName = '';
  this.NewUserModel.LastName = '';
  this.NewUserModel.UserName = '';

}


  CheckUpdateUserName() {
    if (this.UpdateUserModel.UserName.length > 0) {
      this.service.IsUserNameUnique(this.UpdateUserModel.UserName).subscribe(
        res => {
          let response = res as boolean;
          if (!response) {
            this.UpdateUserNameHintText = "User name already in use.";
          }
          else {
            this.UpdateUserNameHintText = "";
          }
        }
      );
    }

  }


  CheckNewUserName() {
    if (this.NewUserModel.UserName.length > 0) {
      this.service.IsUserNameUnique(this.NewUserModel.UserName).subscribe(
        res => {
          let response = res as boolean;
          if (!response) {
            this.NewUserNameHintText = "User name already in use.";
          }
          else {
            this.NewUserNameHintText = "";
          }
        }
      );
    }

  }


  IsUpdateHintTextShown():boolean{
    if(this.UpdateUserNameHintText!==""){
      return true;
    }
    else{
      return false;
    }
  }

  IsNewHintTextShown():boolean{
    if(this.NewUserNameHintText!==""){
      return true;
    }
    else{
      return false;
    }
  }


Nav() {
  this._route.navigate(['login']);
}
}
