import { Component, OnInit, ViewChild } from '@angular/core';
import { MatSort, MatPaginator, MatTableDataSource } from '@angular/material';
import { NotificationService } from 'app/Services/notification.service';
import { RepairService } from 'app/Services/repair.service';
import { Repair } from 'app/Models/repair.model';
import { RepairSearch } from 'app/Models/repair-search.model';
import { HttpErrorResponse } from '@angular/common/http';
import { DatePipe } from '@angular/common';
import { async } from 'q';
import { NgForm } from '@angular/forms';
import { CSVServiceService } from 'app/Services/csvservice.service';
import { FileModel } from 'app/Models/file-model.model';
import Swal from 'sweetalert2';
import { RepairDateModel } from 'app/Models/repair-date-model.model';
import { Router } from '@angular/router';




@Component({
  selector: 'app-repair-setting',
  templateUrl: './repair-setting.component.html',
  styles: []
})
export class RepairSettingComponent implements OnInit {

  

  @ViewChild('DataPaginator', {static: false}) DataPaginator: MatPaginator;
  @ViewChild('ImagePaginator', {static: false}) ImagePaginator: MatPaginator;
  dataSource: any;
  PrintDatasource: any;
  ImageSource: any;
  displayedColumns1: string[];
  displayedColumns2: string[];
  displayedColumns3: string[];
  public FileList:FileModel[];
  public tstImg:Blob = new Blob();
  public HostPath:string="";
  public today:Date;
  public DateFromModel:Date;
  public DateToModel: Date;

  constructor(private RepairService: RepairService, private echo:NotificationService, private _router:Router, private dPipe:DatePipe, private csvService:CSVServiceService) { this.HostPath=this.RepairService.HostUrl+'/images//' }

  public statusList: string[]=['Scheduled','Checked-In','In-Progress','Complete','Checked-Out'];
public repairModel: Repair = {} as any;
public fileList: File[]={} as any;
public searchModel: RepairSearch = {} as any;

 public RepairList: any[];
 




 async ngOnInit() {



this.today=new Date();
  
  this.repairModel= await this.RepairService.SelectedRepairModel; 
 this.searchModel= await this.RepairService.SelectedSearchModel;
 if(this.searchModel.VehicleID===undefined){
  this._router.navigate(['repair/list']);
 }
 
 else{
 this.repairModel.VehicleID= await this.searchModel.VehicleID;
 this.repairModel.CustomerID=await this.searchModel.CustomerID;
 this.FileList= await this.RepairService.GetImages(this.repairModel.CustomerID);


 this.repairModel.UserID= +sessionStorage.getItem('UserID');
 
 this.RepairList=await this.RepairService.GetRepairlist(this.repairModel.VehicleID);
   this.dataSource = new MatTableDataSource(this.RepairList);
   this.PrintDatasource= new MatTableDataSource( this.RepairList);
   this.dataSource.paginator = this.DataPaginator;
  // this.dataSource.sort = this.sort;
   this.displayedColumns1 = ['Date', 'Reason', 'WorkPerformed', 'Suggestion', 'Note', 'NextAppointment','NextAppointmentTime','Status','UserName','Actions'];
   this.displayedColumns3 = ['Date', 'Reason', 'WorkPerformed', 'Suggestion', 'Note', 'NextAppointment','UserName'];

   this.ImageSource = new MatTableDataSource(this.FileList);
   this.ImageSource.paginator = this.ImagePaginator;
   //this.dataSource.sort = this.sort;
   this.displayedColumns2 = ['Data','View', 'Action'];


 }



  }

  


 async AddUpdateRepair(form){

  const formData: FormData = new FormData();
  if(this.fileList!=null){
  for (let i = 0; i < this.fileList.length; i++ ){
    formData.append('FileList', this.fileList[i], this.fileList[i].name);
    }
  }

    for ( var key in this.repairModel ) {
      
      formData.append(key, this.repairModel[key]);
  }

    this.RepairService.AddUpdateRepair(formData).subscribe(
     async res => {
     

        this.echo.Success(res);
        this.RepairList=await this.RepairService.GetRepairlist(this.repairModel.VehicleID);
   this.dataSource = new MatTableDataSource(this.RepairList);
        this.PrintDatasource=this.dataSource;
        this.dataSource.paginator = this.DataPaginator;    
     this.displayedColumns1 = ['Date', 'Reason', 'WorkPerformed', 'Suggestion', 'Note', 'NextAppointment','NextAppointmentTime','Status','UserName','Actions'];

        this.FileList= await this.RepairService.GetImages(this.repairModel.CustomerID);
        this.ImageSource = new MatTableDataSource(this.FileList);
        this.ImageSource.paginator = this.ImagePaginator;  
        this.displayedColumns2 = ['Data','View', 'Action'];


        this.Clear(form);
      },
      err => {
         console.log(err);
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },
    );
  }

Edit(r: Repair)
{

 
  r.Date=this.RepairService.AdjustDate(r.Date);
  if(r.NextAppointment!==null)
  {    
    r.NextAppointment=this.RepairService.AdjustDate(r.NextAppointment);
  } 
  
  
  this.repairModel = r;
 
}
  
Clear(form:NgForm){
  form.resetForm();
  
    this.repairModel.Date=null;
    this.repairModel.Reason='';
    this.repairModel.WorkPerformed='';
    this.repairModel.Suggestion='';
    this.repairModel.Note='';
    this.repairModel.NextAppointment=null;
    this.repairModel.Status='';
    this.fileList=null;

  
  }


applyFilter(filterValue: string) {
  this.dataSource.filter = filterValue.trim().toLowerCase();
}


ExportCSV(){
  var date = new Date();
 let reportHeader=  ['Date', 'Reason', 'WorkPerformed', 'Suggestion', 'Note', 'NextAppointment','NextAppointmentTime','Status','UserName','Actions'];
  this.csvService.Export(this.RepairList,reportHeader,'Visiting History of '+this.searchModel.Make+ ' '+this.searchModel.Model+'  | '+date.toString());

// const printContent = document.getElementById("tryPrint");
// const WindowPrt = window.open('', '', 'left=0,top=0,width=900,height=900,toolbar=0,scrollbars=0,status=0');
// WindowPrt.document.write(printContent.innerHTML);
// WindowPrt.document.close();
// WindowPrt.focus();
// WindowPrt.print();
// WindowPrt.close();
}

onFileSelected(event)
{
  this.fileList = event.target.files;
}


DeleteImage(id)
{
 Swal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonColor: '#00bcd4',
    cancelButtonColor: '#d33',
    focusConfirm: false,
    confirmButtonText: 'Yes, delete it!'
  }).then((result) => {
    if (result.value) {
  
      this.DeleteImageInner(id);
      // Swal.fire(
      //   'Deleted!',
      //   'Repair information has been deleted.',
      //   'success'
      // )
    }
 
  });


}


async DeleteImageInner(id){
 
    this.RepairService.DeleteImage(id).subscribe(
     async res => {
     
      this.echo.Success(res);
        this.FileList= await this.RepairService.GetImages(this.repairModel.CustomerID);
        this.ImageSource = new MatTableDataSource(this.FileList);
        this.ImageSource.paginator = this.ImagePaginator;
        this.displayedColumns2 = ['Data', 'Action'];
      },
      err => {
         console.log(err);
        let e = err as HttpErrorResponse;
        this.echo.Error(e.error.Value);
      },
    );
  }



    

  
 DeleteVisit(id)
{
 Swal.fire({
    title: 'Are you sure?',
    text: "You won't be able to revert this!",
    icon: 'warning',
    showCancelButton: true,
    confirmButtonColor: '#00bcd4',
    cancelButtonColor: '#d33',
    focusConfirm: false,
    confirmButtonText: 'Yes, delete it!'
  }).then((result) => {
    if (result.value) {
   
      this.DeleteVisitInner(id);
      // Swal.fire(
      //   'Deleted!',
      //   'Repair information has been deleted.',
      //   'success'
      // )
    }
 
  });


}




 async DeleteVisitInner(id){
    this.RepairService.DeleteVisit(id).subscribe(
      async res => {
      
       this.echo.Success(res);
       this.RepairList=await this.RepairService.GetRepairlist(this.repairModel.VehicleID);
       this.dataSource = new MatTableDataSource(this.RepairList);   
       this.dataSource.paginator = this.DataPaginator;    
    this.displayedColumns1 = ['Date', 'Reason', 'WorkPerformed', 'Suggestion', 'Note', 'NextAppointment','NextAppointmentTime','Status','UserName','Actions'];

    this.FileList= await this.RepairService.GetImages(this.repairModel.CustomerID);
    this.ImageSource = new MatTableDataSource(this.FileList);
    this.ImageSource.paginator = this.ImagePaginator;   
    this.displayedColumns2 = ['Data', 'Action'];

       },
       err => {
          console.log(err);
         let e = err as HttpErrorResponse;
         this.echo.Error(e.error.Value);
       },
     );
  }


  async GetHistoryByDate(){
    if(this.DateToModel!==undefined && this.DateFromModel !== undefined){
     
      let _RepairDateModel:RepairDateModel= {} as any;
      _RepairDateModel.VehicleID=this.repairModel.VehicleID;
      _RepairDateModel.DateFrom=this.DateFromModel;
      _RepairDateModel.DateTo= this.DateToModel;
  

       await(await this.RepairService.GetRepairlistByDate(_RepairDateModel)).subscribe(
        async res => {
          let repairList: Repair[] ;
          repairList = res as Repair[];
          this.RepairList=repairList;
               this.dataSource = new MatTableDataSource(this.RepairList);
       this.PrintDatasource= new MatTableDataSource( this.RepairList);
       this.dataSource.paginator = this.DataPaginator;
         },
         err => {
        
         },
       );


    }

  }


  IsDateUndefined(){
    if(this.DateToModel!==undefined && this.DateFromModel !== undefined) {
     
     return false;
    }
    return true;
  }

  IsSingleRow() {
    if(this.RepairList!==undefined)
     if(this.RepairList.length===1)
     return true;
     return false;
  }
}
