import { Component, OnInit, ViewChild } from '@angular/core';
import * as Chartist from 'chartist';
import { DashboardService } from 'app/Services/dashboard.service';
import { MatSort, MatPaginator, MatTableDataSource } from '@angular/material';
import { Repair } from 'app/Models/repair.model';
import { RepairSearch } from 'app/Models/repair-search.model';
import { DashboardModel } from 'app/Models/dashboard-model.model';

import { CSVServiceService } from 'app/Services/csvservice.service';
import { log } from 'util';
import { stringify } from 'querystring';
import { StatusModel } from 'app/Models/status-model.model';
import { HttpErrorResponse } from '@angular/common/http';
import { DashboardStatusModel } from 'app/Models/dashboard-status-model.model';
import { NotificationService } from 'app/Services/notification.service';


@Component({
  selector: 'app-dashboard',
  templateUrl: './dashboard.component.html',
  styleUrls: ['./dashboard.component.css']
})
export class DashboardComponent implements OnInit {
  public dateAppointment: Date;
  public dateRepair: Date;

  public reportHeader= ['CustomerFullName','Vehicle','Reason','Phone','WorkPerformed','Note'];
  public reportHeaderAppointment= ['CustomerFullName','Vehicle','Last Visited Reason','Phone','Last Work Performed', 'Note','Time'];

  @ViewChild('paginatorRepairingNow', {static: false}) paginatorRepairingNow: MatPaginator;
  @ViewChild('paginatorRepairedToday', {static: false}) paginatorRepairedToday: MatPaginator;
  @ViewChild('paginatorRepairedYesterday', {static: false}) paginatorRepairedYesterday: MatPaginator;
  @ViewChild('paginatorAppointmentTomorrow', {static: false}) paginatorAppointmentTomorrow: MatPaginator;
  @ViewChild('paginatorAppointmentToday', {static: false}) paginatorAppointmentToday: MatPaginator;



    dataSourceAppointmentTomorrow: any;
    dataSourceAppointmentToday: any;
    dataSourceRepairedToday: any;
    dataSourceRepairedYesterday: any;
    dataSourceRepairingNow: any;

    displayedColumnsRepairingNow: string[];
    displayedColumnsRepairedToday: string[];
    displayedColumnsRepairedYesterday:string[];
    displayedColumnsAppointmentToday: string[];
    displayedColumnsAppointmentTomorrow:string[];

    public Status;
    public repairingNowCount;
    public todayRepairCount;
    public todayAppointmentCount;
    public tomorrowAppointmentCount;
    public yesterdayRepairCount;
    public summary:any;
    public appointmentToday: DashboardModel[] ;
    public repairedToday: DashboardModel[] ;
    public statusList: string[]=['Scheduled','Checked-In','In-Progress','Complete','Checked-Out'];
    public statusListGrid: string[]=['Scheduled','Checked-In','In-Progress','Complete','Checked-Out'];
    public dateStatus: Date;


  constructor(private service: DashboardService, private csvService:CSVServiceService, private echo:NotificationService) {   }

  async ngOnInit() 
  {
   this.Status=this.statusList[2];
   this.dateStatus =  new Date();
     let repairedYesterday: DashboardModel[] ;
     let appointmentTomorrow: DashboardModel[] ;
     let repairingNow: DashboardModel[] ;
  

  this.summary= await this.service.GetSummary();

 appointmentTomorrow=this.summary.AppointmentTomorrow as DashboardModel[];
 this.appointmentToday=this.summary.AppointmentToday as DashboardModel [];
 this.repairedToday=this.summary.RepairedToday as DashboardModel[];
 repairedYesterday=this.summary.RepairedYesterday as DashboardModel[];
 repairingNow=this.summary.RepairingNow as DashboardModel[];


 this.todayRepairCount=this.repairedToday.length;
 this.yesterdayRepairCount=repairedYesterday.length;
 this.tomorrowAppointmentCount=appointmentTomorrow.length;
 this.todayAppointmentCount = this.appointmentToday.length;
 this.repairingNowCount=repairingNow.length;

    this.dataSourceAppointmentTomorrow = new MatTableDataSource(appointmentTomorrow);
    this.dataSourceAppointmentTomorrow.paginator = this.paginatorAppointmentTomorrow;
    // this.dataSourceTomorrow.sort = this.sort;

    this.dataSourceRepairingNow = new MatTableDataSource(repairingNow);
    this.dataSourceRepairingNow.paginator = this.paginatorRepairingNow;
    
    this.dataSourceAppointmentToday = new MatTableDataSource(this.appointmentToday);
    this.dataSourceAppointmentToday.paginator = this.paginatorAppointmentToday;
    // this.dataSourceToday.sort = this.sort;
    
    this.dataSourceRepairedToday = new MatTableDataSource(this.repairedToday);
    this.dataSourceRepairedToday.paginator = this.paginatorRepairedToday;
    // this.dataSourceToday.sort = this.sort;

    this.dataSourceRepairedYesterday = new MatTableDataSource(repairedYesterday);
    this.dataSourceRepairedYesterday.paginator = this.paginatorRepairedYesterday;
    // this.dataSourceYesterday.sort = this.sort;

    this.displayedColumnsRepairingNow = ['CustomerFullName','Vehicle', 'Reason', 'WorkPerformed','Status','Actions'];
    this.displayedColumnsRepairedToday = ['CustomerFullName','Vehicle', 'Reason', 'WorkPerformed'];
    this.displayedColumnsRepairedYesterday = ['CustomerFullName','Vehicle', 'Reason', 'WorkPerformed'];
    this.displayedColumnsAppointmentTomorrow = ['CustomerFullName','Phone','Time','Vehicle','Reason','Note'];
    this.displayedColumnsAppointmentToday = ['CustomerFullName','Phone','Time','Vehicle','Reason','Note'];

 

   }


   ExportRepairingNow(){
    var date = new Date();
    
    this.csvService.Export(this.summary.RepairingNow,this.reportHeader,'Vehicles Repairing Now | '+ date.toString());
  }

   ExportRepairedToday(){
    var date = new Date();
 
    this.csvService.Export(this.summary.RepairedToday,this.reportHeader,'Vehicles Repaired Today | '+date.toString());
  }

  ExportRepairedYesterday(){
   
    var date = new Date();
    date.setDate(date.getDate() - 1); 

     this.csvService.Export(this.summary.RepairedYesterday,this.reportHeader,'Vehicles Repaired Yesterday | '+date.toString());
   }
 
   AppointmentToday(){
    var date = new Date();
 
    this.csvService.Export(this.summary.AppointmentToday,this.reportHeaderAppointment,'Todays Appointment List | '+date.toString());
  }
  AppointmentTomorrow(){
    var date = new Date();
    date.setDate(date.getDate() + 1); 
 
    this.csvService.Export(this.summary.AppointmentTomorrow,this.reportHeaderAppointment,'Tomorrows Appointment List | '+date.toString());
  }
  
 
   applyFilterTomorrow(filterValue: string) {
     this.dataSourceAppointmentTomorrow.filter = filterValue.trim().toLowerCase();
   }

   applyFilterRepairingNow(filterValue: string) {
    this.dataSourceRepairingNow.filter = filterValue.trim().toLowerCase();
  }

   applyFilterToday(filterValue: string) {
    this.dataSourceRepairedToday.filter = filterValue.trim().toLowerCase();
  }

  applyFilterYesterday(filterValue: string) {
    this.dataSourceRepairedYesterday.filter = filterValue.trim().toLowerCase();
  }

  applyFilterAppointmentToday(filterValue: string) {
    this.dataSourceAppointmentToday.filter = filterValue.trim().toLowerCase();
  }


  test(){
    log('sss');
    window.alert('s');
  }

  async GetAppointmentsByDate(){
    let appointments = await this.service.GetAppointmentsByDate(this.dateAppointment);
    this.appointmentToday = appointments as  DashboardModel[];
    // window.alert(this.date );
     this.dataSourceAppointmentToday = new MatTableDataSource(this.appointmentToday);
     this.dataSourceAppointmentToday.paginator = this.paginatorAppointmentToday;
   
   

  }

  async GetRepairListToday(){
    let repairs = await this.service.GetRepairListToday();
    this.repairedToday = repairs as  DashboardModel[];
    // window.alert(this.date );
     this.dataSourceRepairedToday = new MatTableDataSource(this.repairedToday);
     this.dataSourceRepairedToday.paginator = this.paginatorRepairedToday;
     this.todayRepairCount=this.repairedToday.length;
  }


  async GetAppointmentListToday(){
    let repairs = await this.service.GetAppointmentListToday();
    this.appointmentToday = repairs as  DashboardModel[];
    // window.alert(this.date );
     this.dataSourceAppointmentToday = new MatTableDataSource(this.appointmentToday);
     this.dataSourceAppointmentToday.paginator = this.paginatorAppointmentToday;
     this.todayAppointmentCount=this.appointmentToday.length;
  }


  OnFocusStatus(){
    let StatusModel:StatusModel= {} as any;
      StatusModel.Status= this.Status,
      StatusModel.Date= this.dateStatus
    

    var repairingNow:DashboardModel[] ;
      this.service.GetRepairByStatus(StatusModel).subscribe(
        res => {
          let response = res as DashboardModel[];
          this.dataSourceRepairingNow = new MatTableDataSource(response);
          this.dataSourceRepairingNow.paginator = this.paginatorRepairingNow;
        },
        err => {
          // console.log(err);
          let e = err as HttpErrorResponse;
         // this.echo.Error(e.error.Value);
        },
      );


  }

  UpdateStatus(dModel: DashboardModel)
  {
    let sModel: DashboardStatusModel= new DashboardStatusModel();
    sModel.Status=dModel.Status;
    sModel.VisitId=dModel.VisitID;

    this.service.UpdateStatus(sModel).subscribe(
      res => {
        this.OnFocusStatus();
        this.GetRepairListToday();
        this.GetAppointmentListToday();

        this.echo.Success(res);
        // let response = res as DashboardModel[];
        // this.dataSourceRepairingNow = new MatTableDataSource(response);
        // this.dataSourceRepairingNow.paginator = this.paginatorRepairingNow;
       
      },
      err => {
        // console.log(err);
        let e = err as HttpErrorResponse;
       // this.echo.Error(e.error.Value);
      },
    );
  }
}
