import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Repair } from 'app/Models/repair.model';
import { DashboardModel } from 'app/Models/dashboard-model.model';
import { StatusModel } from 'app/Models/status-model.model';
import { DashboardStatusModel } from 'app/Models/dashboard-status-model.model';

@Injectable({
  providedIn: 'root'
})
export class DashboardService {

  HostUrl: string;
  public summary: any = {} as any;
  

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string) {
    this.HostUrl=baseUrl;

   }


 

  async GetSummary() {

    await this.http.get(  this.HostUrl + 'api/dashboard/GetSummary/' )   .toPromise()
    .then(res => this.summary = res )
    return this.summary;
  }



  GetRepairByStatus(_statusModel: StatusModel) {

    let statusModel: StatusModel= {} as any;
    statusModel.Status=_statusModel.Status;
    let date= this.AdjustDate(_statusModel.Date);
    statusModel.Date=date;
    statusModel.DateString=date.toDateString();
    
  
    return this.http.post(this.HostUrl + 'api/dashboard/GetRepairListByStatus', statusModel);
  }


  async GetAppointmentsByDate(_date: Date) {

    let date = this.AdjustDate(_date);
    await this.http.get(  this.HostUrl + 'api/dashboard/GetAppointmentsByDate/' + date.toDateString() )   .toPromise()
    .then(res => this.summary = res )
    return this.summary;
  }

  async GetRepairListToday() {
   
    await this.http.get(  this.HostUrl + 'api/dashboard/GetRepairListToday' )   .toPromise()
    .then(res => this.summary = res )
    return this.summary;
  }

  async GetAppointmentListToday() {
   
    await this.http.get(  this.HostUrl + 'api/dashboard/GetAppointmentListToday' )   .toPromise()
    .then(res => this.summary = res )
    return this.summary;
  }


  AdjustDate(dateToAdjust):Date {
    let newDate= new Date(dateToAdjust);
  //newDate.setMinutes(newDate.getMinutes() - newDate.getTimezoneOffset());
  return newDate;
      }


      
  UpdateStatus(sModel: DashboardStatusModel) {  

   return this.http.post(this.HostUrl + 'api/dashboard/UpdateStatus', sModel);
  }
}
