import { Injectable, Inject } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { MakeModel } from 'app/Models/make-model.model';

@Injectable({
  providedIn: 'root'
})
export class SettingServiceService {

  HostUrl: string;
 
  

  constructor(private http: HttpClient, @Inject('BASE_URL') baseUrl: string) {
    this.HostUrl=baseUrl;

   }


   async GetAllMakeList() {
    let list: MakeModel[] ;
    await this.http.get(  this.HostUrl + 'api/setting/GetAllMakeList')   .toPromise()
    .then(res => list = res as MakeModel[])
    return list;
  }


  async GetActiveMakeList() {
    let list: string[] ;
    await this.http.get(  this.HostUrl + 'api/setting/GetActiveMakeList')   .toPromise()
    .then(res => list = res as string[])
    return list;
  }

  async GetModelList(Make: string) {
    // console.log(Make);
    let list: string[] ;
    await this.http.get(  this.HostUrl + 'api/setting/GetModelList/' + Make)   .toPromise()
    .then(res => list = res as string[])
    return list;
  }


  AddUpdateMake(mModel: MakeModel) {
console.log(mModel.Active);
   return this.http.post(this.HostUrl + 'api/setting/AddUpdateMake', mModel);
  }
}
